import React, { useEffect } from 'react'
import { Box, Typography } from '@mui/material'
import { Controller, useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import CardSmallSelect from './card-small-select/card-small-select'
import { CompanySearchBar } from '../../../components/Sub/Searchers'
import ControlledSocialContact from '../../../components/Sub/FormElements/controlled-social-contact/controlled-social-contact'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import ProjectCreationSchema from '../../../utils/validation-schemas/project-creation-schemas'
import { setIsLockNextButton, setProjectsValues } from '../../../services/slices/project-creation-slice'
import StudentsSearchBar from '../../../components/Sub/Searchers/StudentsSearchBar/StudentsSearchBar'

const TabPC4 = () => {
  const dispatch = useAppDispatch()
  const { selectedCustomerType } = useAppSelector((store) => store.projectCreationPage)
  const {
    control,
    handleSubmit,
    setValue,
    watch,
    register,
    trigger,
    getValues,
    setError,
    reset,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {},
    mode: 'onChange',
    resolver: yupResolver(ProjectCreationSchema.tabPC4),
  })

  const company = watch('company_name')

  useEffect(() => {
    if (isValid && selectedCustomerType && company) {
      dispatch(setIsLockNextButton(false))
      dispatch(setProjectsValues({ fieldName: 'tabPC4Values', values: getValues() }))
    } else {
      dispatch(setIsLockNextButton(true))
    }
  }, [isValid, selectedCustomerType, company])

  useEffect(() => {
    setValue('company_name', undefined, { shouldValidate: true, shouldDirty: false })
  }, [selectedCustomerType])

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        height: '100%',
      }}
    >
      <Typography variant="h3" component="h1" mt="23px">
           
      </Typography>
      <Box mt="33px" sx={{ width: 387, display: 'flex', flexDirection: 'column' }}>
        <Typography variant="body2" mb="12px" component="p">
          
        </Typography>
        <CardSmallSelect />
        <Typography variant="body2" mt="19px" mb="3px">
             
        </Typography>
        <Controller
          name="company_name"
          control={control}
          render={({ field }) =>
            selectedCustomerType?.db_name === 'Company' ? (
              <CompanySearchBar value={field?.value} onChange={field?.onChange} error={!!errors?.company_name} />
            ) : (
              <StudentsSearchBar
                disabled={!selectedCustomerType}
                value={field?.value}
                onChange={field?.onChange}
                error={!!errors?.company_name}
              />
            )
          }
        />
        <Typography variant="body2" mt="20px" mb="17px">
            
        </Typography>
        <ControlledSocialContact
          variant="telegram"
          width={387}
          control={control}
          error={errors?.telegram?.message || ''}
          name="telegram"
          placeholder=" "
        />
        <Typography variant="caption2" mt="-11px" mb="19px" sx={{ color: '#797981' }}>
                 Telegram.    
        </Typography>
        <ControlledSocialContact
          variant="email"
          width={387}
          control={control}
          error={errors?.email?.message || ''}
          name="email"
          placeholder=" ."
        />
        <ControlledSocialContact
          variant="whatsapp"
          width={387}
          control={control}
          error={errors?.whatsapp?.message || ''}
          name="whatsapp"
          placeholder=" "
        />
      </Box>
    </Box>
  )
}
export default TabPC4
