import React, { FC, useEffect } from 'react'
import { Box, Typography } from '@mui/material'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import theme from '../../../../utils/themeMUI5'
import { ControlledSelectField, ControlledTextField } from '../../../Sub/FormElements'
import { termsOfUseSchema } from '../../../../utils/schemas'
import { TTeamItem } from '../../../../services/types/workshop-projects-list-page-types/workshop-projects-list-page-types'
import { useAppDispatch } from '../../../../services/store/hooks'
import { setIsLockNextButton } from '../../../../services/slices/project-creation-slice'
import ProjectCreationSchema from '../../../../utils/validation-schemas/project-creation-schemas'

type Props = {
  item: TTeamItem
}

const WorkshopTeamRequirementsItem: FC<Props> = ({ item }) => {
  const dispatch = useAppDispatch()
  const {
    control,
    setValue,
    handleSubmit,
    setError,
    watch,
    clearErrors,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {
      status: 1,
      requirements: '',
    },
    resolver: yupResolver(ProjectCreationSchema.tabPC23),
    mode: 'onChange',
  })

  const selectedStatus = watch('status')

  // TODO      ,   
  const statuses = [
    {
      id: 1,
      name: ' ',
    },
    {
      id: 2,
      name: ' ',
    },
  ]

  const handleStatusChange = (e: any) => {
    setValue('status', e.target.value)
  }

  useEffect(() => {
    if (isValid) {
      dispatch(setIsLockNextButton(false))
      // TODO    ,        ,   
    } else {
      dispatch(setIsLockNextButton(true))
    }
  }, [isValid])

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', gap: '20px' }}>
      <Typography variant="body3" color={theme.color.blueMain}>
        {item.title}
      </Typography>
      <Box sx={{ display: 'flex', gap: '16px', width: '100%' }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
          <ControlledSelectField
            maxWidth={240}
            disabled
            labelTitle=""
            value={1}
            onChange={function (arg0: any): void {
              console.log()
            }}
            items={[{ id: 1, name: item.title }]}
          />
          <ControlledSelectField
            maxWidth={240}
            labelTitle=" "
            value={selectedStatus}
            onChange={handleStatusChange}
            items={statuses}
          />
        </Box>
        <ControlledTextField
          control={control}
          fieldName="requirements"
          multiline
          rows={10}
          width={256}
          maxWidth={256}
          labelTitle=""
          placeholder=": Figma,    , Adobe Photoshop, Adobe After Effect"
          error={!!errors.requirements?.message}
          helperText={errors.requirements?.message}
        />
      </Box>
    </Box>
  )
}

export default React.memo(WorkshopTeamRequirementsItem)
