import React, { useEffect } from 'react'
import { Box } from '@mui/system'
import CardItem from './card-item'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setIsLockNextButton, setProjectType } from '../../../../services/slices/project-creation-slice'
import { IDirectoriesProjectsTypes } from '../../../../services/types/workshop-projects-list-page-types/workshop-projects-list-page-types'

const CardSelect = () => {
  const dispatch = useAppDispatch()
  const { project_types, selectedProjectType } = useAppSelector((store) => store.projectCreationPage)

  const onClick = (type: IDirectoriesProjectsTypes) => {
    dispatch(setProjectType(type))
  }

  useEffect(() => {
    if (!selectedProjectType?.id) {
      dispatch(setIsLockNextButton(true))
    } else {
      dispatch(setIsLockNextButton(false))
    }
  }, [selectedProjectType])

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        gap: '16px',
      }}
    >
      {project_types.map((item) => (
        <CardItem
          key={item.id}
          header={item.type}
          content={item.comment}
          isActive={selectedProjectType?.id === item.id}
          onClick={() => onClick(item)}
        />
      ))}
    </Box>
  )
}

export default CardSelect
