import React, { useEffect } from 'react'
import { Box, ThemeProvider, Typography } from '@mui/material'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import theme from '../../../utils/themeMUI5'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { ControlledTextField } from '../../../components/Sub/FormElements'
import ProjectCreationSchema from '../../../utils/validation-schemas/project-creation-schemas'
import { setIsLockNextButton, setProjectsValues } from '../../../services/slices/project-creation-slice'

const TabPC31 = () => {
  const dispatch = useAppDispatch()
  const {
    control,
    handleSubmit,
    setError,
    watch,
    clearErrors,
    formState: { errors, isValid },
  } = useForm({
    resolver: yupResolver(ProjectCreationSchema.tabPC31),
    mode: 'onChange',
  })
  const comment = watch('customer_comment')

  useEffect(() => {
    if (isValid) {
      dispatch(setIsLockNextButton(false))
      dispatch(setProjectsValues({ fieldName: 'customer_comment', values: comment }))
    } else {
      dispatch(setIsLockNextButton(true))
    }
  }, [isValid, comment])

  return (
    <ThemeProvider theme={theme}>
      <Box
        sx={{
          width: '100%',
          // height: '484px',
          height: '100%',
          display: 'flex',
          flexDirection: 'column',
          boxSizing: 'border-box',
          padding: '32px 0 53px 122px',
        }}
      >
        <Typography sx={{ width: '416px' }} variant="h3">
            
        </Typography>
        <Box
          sx={{
            position: 'relative',
            zIndex: 1,
            width: '100%',
            maxWidth: '813px',
            boxSizing: 'border-box',
            padding: 0,
            overflowY: 'scroll',
            scrollbarWidth: 'thin',
            scrollbarColor: '#1a1b2233 #ffffff',
            '&::-webkit-scrollbar': {
              width: 4,
              marginRight: 5,
            },
            '&::-webkit-scrollbar-thumb': {
              backgroundColor: '#1a1b2233',
            },
            display: 'flex',
            gap: '20px',
            margin: '32px 0 0',
          }}
          component="ul"
        >
          <ControlledTextField
            control={control}
            fieldName="customer_comment"
            error={!!errors.customer_comment?.message}
            helperText={errors.customer_comment?.message}
            multiline
            rows={15}
            width={508}
            maxWidth={508}
            placeholder="           "
            maxLength={5000}
            minLength={80}
            symbolsCount={comment?.length || 0}
          />
          <Box
            sx={{
              width: '285px',
              height: '320px',
              borderRadius: '8px',
              backgroundColor: '#F9F9F9',
              boxSizing: 'border-box',
              padding: '24px 20px 29px',
              display: 'flex',
              flexDirection: 'column',
              gap: '16px',
            }}
          >
            <Typography variant="body3">   ?</Typography>
            <Typography>
              <span style={{ color: theme.color.blueMain }}> </span>   ,   
                ,     
            </Typography>
            <Typography>
              <span style={{ color: theme.color.blueMain }}> </span>    , 
                ,     
            </Typography>
            <Typography>
              <span style={{ color: theme.color.blueMain }}> </span>    ,  
            </Typography>
          </Box>
        </Box>
      </Box>
    </ThemeProvider>
  )
}

export default React.memo(TabPC31)
