import React from 'react'
import { Box } from '@mui/system'
import { Avatar, Typography } from '@mui/material'

interface CardItemProps {
  header: string
  content: string
  avatar?: string
  isActive?: boolean
  onClick?: () => void
}

const CardItem = ({ header, content, avatar, isActive = false, onClick }: CardItemProps) => (
  <Box
    sx={{
      width: 611,
      height: 104,
      outline: `1px solid ${isActive ? '#1D6BF3' : '#DDE0E4'}`,
      borderRadius: '16px',
      padding: '20px 32px',
      display: 'flex',
      justifyContent: 'space-between',
      transition: 'all .5s ease',
      '&:hover': {
        outline: '1px solid #1D6BF3',
        cursor: 'pointer',
      },
    }}
    onClick={onClick}
  >
    <Box sx={{ maxWidth: 450 }}>
      <Typography
        variant="body3"
        sx={{ color: isActive ? '#1D6BF3' : '#1A1B22', transition: 'all .5s ease' }}
        component="h3"
      >
        {header}
      </Typography>
      <Typography variant="body" sx={{ color: '#797981', marginTop: '3px' }} component="p">
        {content}
      </Typography>
    </Box>
    <Avatar variant="rounded" sx={{ width: 60, height: 60 }} src={avatar} />
  </Box>
)

export default CardItem
