import React, { useEffect, useState } from 'react'
import { Box, Typography } from '@mui/material'
import { Controller, useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import ControlledTextField from '../../../components/Sub/FormElements/controlled-text-field/controlled-text-field'
import CheckboxItem from '../../../components/Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import ProjectCreationSchema from '../../../utils/validation-schemas/project-creation-schemas'
import { useAppDispatch } from '../../../services/store/hooks'
import { setIsLockNextButton, setProjectsValues } from '../../../services/slices/project-creation-slice'
import { newCohortSchema } from '../../../utils/schemas'

const TabPC2 = () => {
  const dispatch = useAppDispatch()
  const {
    control,
    handleSubmit,
    watch,
    trigger,
    setValue,
    getValues,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {},
    mode: 'onChange',
    resolver: yupResolver(ProjectCreationSchema.tabPC2),
  })

  const descriptionValue = watch('description')
  const isDocNotRequired = watch('isDocNotRequired')
  const [descriptionTextCount, setDescriptionTextCount] = useState(0)

  useEffect(() => {
    setDescriptionTextCount(descriptionValue ? descriptionValue.length : 0)
  }, [descriptionValue])

  useEffect(() => {
    if (!isValid) {
      dispatch(setIsLockNextButton(true))
    } else {
      // TODO  ,   .       
      dispatch(setIsLockNextButton(false))
      dispatch(setProjectsValues({ fieldName: 'tabPC2Values', values: getValues() }))
    }
  }, [isValid])

  useEffect(() => {
    trigger('tech_doc_link')
    setValue('tech_doc_link', '')
  }, [isDocNotRequired])

  return (
    <Box
      onSubmit={handleSubmit(() => null)}
      id="form-TabPC2"
      component="form"
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        height: '100%',
      }}
    >
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',

          height: '100%',
          width: '813px',
        }}
      >
        <Typography variant="h3" component="h1" mt="23px">
              ,    ?
        </Typography>
        <Box
          mt="33px"
          sx={{
            display: 'flex',
            gap: '20px',
          }}
        >
          <Box sx={{ width: '508px' }}>
            <ControlledTextField
              labelTitle=" "
              fieldName="project_name"
              control={control}
              error={!!errors.project_name?.message}
              helperText={errors.project_name?.message}
              placeholder=" "
              maxWidth={508}
            />
            <ControlledTextField
              fieldName="description"
              control={control}
              error={!!errors.description?.message}
              helperText={errors.description?.message}
              placeholder="     "
              multiline
              rows={8}
              maxWidth={508}
              symbolsCount={descriptionTextCount}
              maxLength={5000}
              minLength={80}
            />
            <Box sx={{ display: 'flex' }}>
              <ControlledTextField
                labelTitle="  ."
                fieldName="tech_doc_link"
                control={control}
                error={!!errors.tech_doc_link?.message}
                helperText={errors.tech_doc_link?.message}
                placeholder=" "
                maxWidth={338}
                disabled={isDocNotRequired}
              />
              <Box sx={{ alignSelf: 'center', marginLeft: 'auto' }}>
                <Controller
                  name="isDocNotRequired"
                  control={control}
                  render={({ field }) => (
                    <CheckboxItem
                      {...field}
                      checkboxLabel=" "
                      checked={isDocNotRequired}
                      margin={{ marginBottom: 0 }}
                    />
                  )}
                />
              </Box>
            </Box>
          </Box>
          <Box
            sx={{
              width: '285px',
              height: '344px',
              borderRadius: '8px',
              backgroundColor: '#F9F9F9',
              padding: '24px 20px',
            }}
          >
            <Typography variant="body3" component="h2">
                 ?
            </Typography>
            <Typography variant="body" component="p" mt="18px">
              <Typography sx={{ color: '#1D6BF3', fontWeight: 500 }} component="span">
                 
              </Typography>{' '}
                ( )   ()   ()
            </Typography>
            <Typography variant="body" component="p" mt="18px">
              <Typography sx={{ color: '#1D6BF3', fontWeight: 500 }} component="span">
                 
              </Typography>
              &nbsp;&ndash;&nbsp;  ,    ,    
              
            </Typography>
            <Typography variant="body" component="p" mt="18px">
              <Typography sx={{ color: '#1D6BF3', fontWeight: 500 }} component="span">
                 
              </Typography>
              &nbsp;&ndash;&nbsp;  ,  
            </Typography>
          </Box>
        </Box>
      </Box>
    </Box>
  )
}
export default TabPC2
