import React, { useEffect } from 'react'
import { Box, ThemeProvider, Typography } from '@mui/material'
import theme from '../../../utils/themeMUI5'
import WorkshopTeamItem from '../../../components/Main/ProjectCreationPage/WorkshopTeamItem/WorkshopTeamItem'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { setIsLockNextButton, setProjectsValues } from '../../../services/slices/project-creation-slice'

const TabPC21 = () => {
  const dispatch = useAppDispatch()
  const { teamItems } = useAppSelector((store) => store.projectCreationPage)

  useEffect(() => {
    if (teamItems?.length) {
      const filteredItems = teamItems.some((item) => item.isActive)
      if (filteredItems) {
        dispatch(setIsLockNextButton(false))
        dispatch(
          setProjectsValues({ fieldName: 'selectedFaculties', values: teamItems.filter((item) => item.isActive) }),
        )
      } else {
        dispatch(setIsLockNextButton(true))
      }
    }
  }, [teamItems])

  return (
    <ThemeProvider theme={theme}>
      <Box
        sx={{
          width: '100%',
          height: '100%',
          display: 'flex',
          flexDirection: 'column',
          boxSizing: 'border-box',
          padding: '32px 156px 82px 122px',
        }}
      >
        <Typography variant="h3">  ,     </Typography>
        <Typography
          color={theme.color.black500}
          variant="body1"
          sx={{ margin: '12px 0 22px', lineHeight: '20px', width: '480px' }}
        >
             4           IT- 
           
        </Typography>
        <Box
          sx={{
            position: 'relative',
            zIndex: 1,
            width: '100%',
            // minHeight: '272px',
            height: '100%',
            boxSizing: 'border-box',
            padding: '10px 0 0 0',
            overflowY: 'scroll',
            scrollbarWidth: 'thin',
            scrollbarColor: '#1a1b2233 #ffffff',
            '&::-webkit-scrollbar': {
              width: 4,
              marginRight: 5,
            },
            '&::-webkit-scrollbar-thumb': {
              backgroundColor: '#1a1b2233',
            },
            display: 'grid',
            gridTemplateColumns: '150px 150px 150px 150px',
            gap: '12px',
            margin: 0,
          }}
          component="ul"
        >
          {teamItems?.map((item) => (
            <WorkshopTeamItem item={item} />
          ))}
        </Box>
      </Box>
    </ThemeProvider>
  )
}

export default React.memo(TabPC21)
