import React, { FC } from 'react'
import { Box, Typography } from '@mui/material'
import noTeamImage from '../../../../assets/noTeamImage.svg'
import theme from '../../../../utils/themeMUI5'
import { changeActiveStatus } from '../../../../services/slices/project-creation-slice'
import { useAppDispatch } from '../../../../services/store/hooks'
import { TTeamItem } from '../../../../services/types/workshop-projects-list-page-types/workshop-projects-list-page-types'

type Props = {
  item: TTeamItem
}

const WorkshopTeamItem: FC<Props> = ({ item }) => {
  const dispatch = useAppDispatch()
  return (
    <Box
      onClick={() => dispatch(changeActiveStatus(item.id))}
      key={item.id}
      component="li"
      sx={{
        border: '1px solid #DDE0E4',
        borderColor: item.isActive ? theme.color.blueMain : undefined,
        borderRadius: '4px',
        width: '150px',
        height: '125px',
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        gap: '43px',
        boxSizing: 'border-box',
        padding: '12px 12px 8px',
        cursor: 'pointer',
        '&:hover': { borderColor: '#5A9BFF' },
      }}
    >
      <Box
        sx={{ width: '30px', height: '30px' }}
        component="img"
        alt="   "
        src={item.teamImage || noTeamImage}
      />
      <Typography color={item.isActive ? theme.color.blueMain : undefined} variant="body2">
        {item.title}
      </Typography>
      {item.isMustHave && (
        <Box
          sx={{
            position: 'absolute',
            top: '-10px',
            right: '10px',
            backgroundColor: 'hsla(33, 100%, 79%, 1)',
            borderRadius: '8px',
            zIndex: 99999,
            width: '77px',
            height: '20px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <Typography variant="caption2">must have</Typography>
        </Box>
      )}
    </Box>
  )
}

export default WorkshopTeamItem
