import React, { useEffect } from 'react'
import { Box, Typography } from '@mui/material'
import { Controller, useForm } from 'react-hook-form'
import FormHelperText from '@mui/material/FormHelperText'
import { yupResolver } from '@hookform/resolvers/yup'
import ControlledSelectField from '../../../components/Sub/FormElements/controlled-select-field/controlled-select-field'
import ButtonCounter from '../../../components/Sub/FormElements/ButtonCounter/ButtonCounter'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import ProjectCreationSchema from '../../../utils/validation-schemas/project-creation-schemas'
import { setIsLockNextButton, setProjectsValues } from '../../../services/slices/project-creation-slice'

const TabPC3 = () => {
  const dispatch = useAppDispatch()
  const { project_spheres, project_directions } = useAppSelector((store) => store.projectCreationPage)

  const {
    control,
    handleSubmit,
    setValue,
    watch,
    register,
    trigger,
    getValues,
    setError,
    reset,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {},
    mode: 'onChange',
    resolver: yupResolver(ProjectCreationSchema.tabPC3),
  })

  useEffect(() => {
    if (!isValid) {
      dispatch(setIsLockNextButton(true))
    } else {
      dispatch(setIsLockNextButton(false))
      dispatch(setProjectsValues({ fieldName: 'tabPC3Values', values: getValues() }))
    }
  }, [isValid])

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        height: '100%',
      }}
    >
      <Typography variant="h3" component="h1" mt="23px">
            
      </Typography>
      <Box mt="33px">
        <Controller
          name="sphere"
          control={control}
          render={({ field: { name, value, onChange } }) => (
            <ControlledSelectField
              labelTitle=" "
              tipContent="  "
              name={name}
              value={value || []}
              onChange={onChange}
              error={!!errors?.sphere?.message}
              helperText={errors?.sphere?.message}
              items={project_spheres}
              displayEmpty
              maxWidth={404}
            />
          )}
        />
        <Controller
          name="direction"
          control={control}
          render={({ field: { name, value, onChange } }) => (
            <ControlledSelectField
              labelTitle=" "
              tipContent="  "
              name={name}
              value={value || []}
              onChange={onChange}
              error={!!errors?.direction?.message}
              helperText={errors?.direction?.message}
              items={project_directions}
              displayEmpty
              maxWidth={404}
            />
          )}
        />
        <Box mt="21px">
          <Controller
            name="pet_project_count"
            control={control}
            render={({ field: { name, value, onChange } }) => (
              <ButtonCounter
                labelTitle="   "
                value={value || 0}
                onChange={onChange}
                isHorizontal
                width={404}
              />
            )}
          />
          <FormHelperText sx={{ color: 'red' }}>
            {errors?.pet_project_count?.message ? errors?.pet_project_count?.message : ' '}
          </FormHelperText>
        </Box>
      </Box>
    </Box>
  )
}
export default TabPC3
