import React from 'react'
import { Box } from '@mui/system'
import CardSmallItem from './card-small-item'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setCustomerType } from '../../../../services/slices/project-creation-slice'
import { ICustomerType } from '../../../../services/types/workshop-projects-list-page-types/workshop-projects-list-page-types'

const CardSmallSelect = () => {
  const dispatch = useAppDispatch()
  const { customer_types, selectedCustomerType } = useAppSelector((store) => store.projectCreationPage)

  const onClick = (item: ICustomerType) => {
    dispatch(setCustomerType(item))
  }

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'row',
        gap: '16px',
      }}
    >
      {customer_types.map((item) => (
        <CardSmallItem
          key={item.type}
          header={item.db_name === 'StudentProfile' ? '' : ''}
          isActive={item.type === selectedCustomerType?.type}
          onClick={() => onClick(item)}
        />
      ))}
    </Box>
  )
}

export default CardSmallSelect
