import * as yup from 'yup'
import { urlOption, telegramOption } from '../schemas'
import { FILE_SIZE, PHONE_REG_EXP } from '../constants'

class ProjectCreationSchema {
  static readonly tabPC2 = yup.object().shape({
    project_name: yup.string().required('  '),
    description: yup
      .string()
      .required('  ')
      .min(80, `   80-5000 `)
      .max(5000, `   80-5000 `),
    tech_doc_link: urlOption.required('   ').when('isDocNotRequired', {
      is: true,
      then: (theme) => theme.notRequired(),
    }),
  })

  static readonly tabPC3 = yup.object().shape({
    sphere: yup.string().required(' '),
    direction: yup.string().required(' '),
    pet_project_count: yup
      .number()
      .required('  ')
      .min(1, '     1 '),
  })

  static readonly tabPC4 = yup.object().shape({
    // TODO    
    // company_name: yup.lazy((value) => {
    //   switch (typeof value) {
    //     case 'object':
    //       return yup.object().required(' ').nullable(true) // schema for object
    //     case 'string':
    //       return yup.string().required(' ').nullable(true) // schema for string
    //     default:
    //       return yup.object().required(' ').nullable(true)
    //   }
    // }),
    telegram: telegramOption,
    email: yup.string().email('    ').nullable(true).trim(),
    whatsapp: yup
      .string()
      .nullable()
      .transform((curr, orig) => (orig === '' ? null : curr))
      .matches(PHONE_REG_EXP, '   ')
      .trim(),
  })

  static readonly tabPC23 = yup.object().shape({
    requirements: yup
      .string()
      .required(' ')
      .min(3, `   3-500 `)
      .max(500, `   3-500 `),
  })

  static readonly tabPC31 = yup.object().shape({
    customer_comment: yup
      .string()
      .required(' ')
      .min(3, `   3-500 `)
      .max(500, `   3-500 `),
  })

  static readonly tabPC32 = yup.object().shape({
    big_cover: yup
      .mixed()
      .test(
        'fileSize',
        `   ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
    small_cover: yup
      .mixed()
      .test(
        'fileSize',
        `   ${FILE_SIZE / 1048576} MB`,
        (value) => !value || typeof value === 'string' || (value && value.size <= FILE_SIZE),
      ),
  })
}

export default ProjectCreationSchema
