import React from 'react'
import { Box } from '@mui/system'
import { Avatar, Typography } from '@mui/material'

interface CardSmallItemProps {
  header: string
  avatar?: string
  isActive?: boolean
  onClick?: () => void
}

const CardSmallItem = ({ header, avatar, isActive = false, onClick }: CardSmallItemProps) => (
  <Box
    sx={{
      width: 187,
      height: 125,
      outline: `1px solid ${isActive ? '#1D6BF3' : '#DDE0E4'}`,
      borderRadius: '4px',
      padding: '12px 12px 24px 12px',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'space-between',
      transition: 'all .5s ease',
      '&:hover': {
        outline: '1px solid #1D6BF3',
        cursor: 'pointer',
      },
    }}
    onClick={onClick}
  >
    <Avatar variant="rounded" sx={{ width: 30, height: 30 }} src={avatar} />
    <Typography
      variant="body2"
      sx={{ fontWeight: 400, color: isActive ? '#1D6BF3' : '#1A1B22', transition: 'all .5s ease' }}
      component="h3"
    >
      {header}
    </Typography>
  </Box>
)

export default CardSmallItem
